        <h1 class="">Checks</h1>

    <div id="app">
             

     <div class="my-3 p-3 bg-body rounded shadow-sm ">
    <h6 class="border-bottom pb-2 mb-0">Approved</h6>
    <div v-for="(item) in approved" :key="item.id"
    class="d-flex text-muted pt-3 itemdisp">
      <svg class="bd-placeholder-img flex-shrink-0 me-2 rounded" width="32" height="32" xmlns="http://www.w3.org/2000/svg" role="img" aria-label="Placeholder: 32x32" preserveAspectRatio="xMidYMid slice" focusable="false"><title>Placeholder</title><rect width="100%" height="100%" fill="#007bff"/><text x="50%" y="50%" fill="#007bff" dy=".3em">32x32</text></svg>

      <div  
      class="pb-3 mb-0 small lh-sm border-bottom w-100">
        <div class="d-flex justify-content-between">
          <strong class="text-primary">{{item.id}} {{item.description}}</strong>
          <a href="#">${{item.amount}}</a>
        </div>
        <span class="d-block text-primary">{{item.date}}</span>
      </div>
    </div>
  
  </div>
  

                <h1 class="">Pending</h1>
      <div v-for="(item) in pending" :key="item.id">
           
        {{item.id}}
        {{item.description}}
         {{item.date}}
         {{item.type}}
         {{item.amount}}
     
      </div>   

        <h1 class="">Rejected</h1>
      <div v-for="(item) in pending" :key="item.id">
           
        {{item.id}}
        {{item.description}}
         {{item.date}}
         {{item.type}}
         {{item.amount}}
     
      </div>
    </div>
       

</main>
</template>

<script>
import $ from 'jquery'
import endpoint_res from '../resource/endpointresouce';
import Cookie from 'js-cookie';

export default {
    name: 'Checks',
    data(){return {
                  approved:"",
                  pending:"",
                  rejected:""
                  };
    },
    methods: {
      gettransactions(){ 
         const token = Cookie.get('bank_token');
    
        fetch( endpoint_res.MY_CHECKS_ENDPOINT, { 
          method:'GET',
           headers: {
              'Content-Type':'application/json' ,
              'Access':'application/json',
              'Authorization' :'Bearer '+token
            }
            
        })
        .then(response => response.json())
        .then(res => {
          this.approved = res.approved;
          this.pending = res.pending;
          this.rejected = res.rejected;
        }).catch(error => {
                console.log(error);
                alert("Unauthorized!");
                this.$router.push({path: '/login', }); 
              });
      },
  
      
    },
    mounted () {
   // Your JQuery code here
   $(document).ready(function(){
    $('a[data-bs-toggle="tab"]').on("shown.bs.tab", function(e){
        var activeTab = $(e.target).text(); // Get the name of active tab
        var previousTab = $(e.relatedTarget).text(); // Get the name of previous active tab
        $(".active-tab span").html(activeTab);
        $(".previous-tab span").html(previousTab);
    });
});
  },
    
    beforeMount(){
      this.gettransactions();
     },
}; 

</script>



