<template>
  <v-card class="mx-auto" max-width="500">
    <v-toolbar color="pink" dark>
      <v-toolbar-title>Expenses</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-checkbox-marked-circle</v-icon>
      </v-btn>
    </v-toolbar>

    <v-list two-line>
      <v-list-item-group active-class="pink--text" multiple>
        <template v-for="(item, index) in transaction">
          <v-list-item :key="item.id" class="list-item">
            <template>
              <v-list-item-content>
                <v-list-item-title
                  v-text="item.description"
                ></v-list-item-title>

                <v-list-item-subtitle
                  class="text--primary"
                  v-text="item.headline"
                ></v-list-item-subtitle>

                <v-list-item-subtitle v-text="item.date"></v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                <v-list-item-action-text
                 v-bind:class="{'amount-text deposit': item.type['P'],  'amount-text payment': !item.type['P']}" >
                  {{item.type === "P" ? "-" : "+"}}
                  {{item.amount}}
                </v-list-item-action-text>
              </v-list-item-action>
            </template>
          </v-list-item>

          <v-divider
            v-if="index < transaction.length - 1"
            :key="index"
          ></v-divider>
        </template>
      </v-list-item-group>
    </v-list>
  </v-card>
</template>


<script>
import endpoint_res from '../resource/endpointresouce';
import Cookie from 'js-cookie';

export default {
    name: 'Expenses',
    data(){return {
                  transaction: "valor",
                  };
    },
    methods: {
      gettransactions(){ 
         const token = Cookie.get('bank_token');
       
      
        fetch( endpoint_res.EXPENSES_ENDPOINT, { 
          method:'GET',
           headers: {
              'Content-Type':'application/json' ,
              'Access':'application/json',
              'Authorization' :'Bearer '+token
            }
            
        })
        .then(response => response.json())
        .then(res => {
          this.transaction = res.data;
          console.log(this.transaction);
        }).catch(error => {
                console.log(error);
                alert("Unauthorized!");
                this.$router.push({path: '/login', }); 
              });
      
      },      
    },
    
    beforeMount(){
      this.gettransactions();
     },
}; 

</script>



<style>
.form-signin {
  width: 100%;
  padding: 15px;
  margin: auto;
}

.amount-text {
  font-size: 20px !important;
  font-weight: bold;
}
.payment {
  color:rgb(238, 77, 77) !important;
}
.deposit {
  color:rgb(77, 179, 238) !important;
}
.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-floating:focus-within {
  z-index: 2;
}

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>